import AnimeList from "./components/AnimeList.svelte";
import Anime from "./components/Anime.svelte";
import NotFound from "./components/NotFound.svelte";

const currentUrl = location.href.split('/').slice(3)[0];

const routes = [
    {
        name: `${currentUrl}/`,
        component: AnimeList
    },
    {
        name: `${currentUrl}/anime/:id`,
        component: Anime
    },
    {
        name: '404',
        path: '404',
        component: NotFound
      }
]

export { routes }